<template>
  <div id="main"></div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    fillChart1(data) {
      let chart_projectLoad = this.$echarts.init(
        document.getElementById("main")
      );
      let dateList = data.map(function (item) {
        return item[0];
      });
      let valueList = data.map(function (item) {
        return item[1];
      });
      let option = {
        visualMap: {
          show: false,
          type: "continuous",
          seriesIndex: 0,
        },
        dataZoom: [
          {},
          {
            type: "inside",
          },
        ],
        tooltip: {
          trigger: "axis",
        },
        xAxis: {
          data: dateList,
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
        },
        yAxis: {
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
        },
        grid: {},
        series: {
          type: "line",
          showSymbol: false,
          data: valueList,
          smooth: true,
          areaStyle: {
            color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: "rgba(236, 202, 149, 0.85)",
              },
              {
                offset: 1,
                color: "rgba(236, 202, 149, 0.55)",
              },
            ]),
          },
        },
      };

      chart_projectLoad.setOption(option);
    },
  },
};
</script>
<style scoped>
#main {
  width: 100%;
  height: 100%;
}
</style>