<template>
  <div class="energyTrendAnalysis">
    <div class="right border-box">
      <div class="search chartSearch">
        <div>
          <span>园区</span>
          <el-select v-model="areaVal" placeholder="" @change="companyListFun">
            <el-option
              v-for="item in areaOptions"
              :key="item.areaId"
              :label="item.areaName"
              :value="item.areaId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>企业</span>
          <el-select
            v-model="companyVal"
            placeholder=""
            @change="companyelecstationFun"
          >
            <el-option
              v-for="item in companyOptions"
              :key="item.companyId"
              :label="item.companyName"
              :value="item.companyId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>电站</span>
          <el-select
            v-model="elecstationVal"
            placeholder=""
            @change="elecEqmListFun"
          >
            <el-option
              v-for="item in elecstationOptions"
              :key="item.elecStationId"
              :label="item.elecStationName"
              :value="item.elecStationId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>设备名称</span>
          <el-select v-model="elecEqmVal" placeholder="" @change="pointListFun">
            <el-option
              v-for="item in elecEqmOptions"
              :key="item.elecEqmId"
              :label="item.elecEqmName"
              :value="item.elecEqmId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>测量点名称</span>
          <el-select v-model="pointVal" placeholder="">
            <el-option
              v-for="item in pointListOptions"
              :key="item.elecPointId"
              :label="item.pointName"
              :value="item.elecPointId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <el-date-picker
            v-model="date1"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd HH:mm:ss"
          >
          </el-date-picker>
        </div>
        <button class="searchBth" @click="zlchartsFun()">查询</button>
      </div>
      <div class="tabel">
        <el-table :data="tableData" style="width: 100%" height="250">
          <el-table-column prop="CompanyName1" label="企业名称" width="170">
          </el-table-column>
          <el-table-column prop="ElecStationName1" label="电站名称">
          </el-table-column>
          <el-table-column prop="Position1" label="位置"> </el-table-column>
          <el-table-column prop="ElecPointName1" label="测量点名称">
          </el-table-column>
          <el-table-column prop="StartValue1" label="起始值"> </el-table-column>
          <el-table-column prop="EndValue1" label="结束值"> </el-table-column>
          <el-table-column prop="UseValue1" label="总用量"> </el-table-column>
          <el-table-column prop="UnitId1" label="单位"> </el-table-column>
        </el-table>
      </div>
      <div class="border-box chart" style="flex-grow: 1">
        <div class="tabAll">
          <div></div>
          <div>
            <div
              v-for="(item, index) in timeTab"
              :key="index"
              :class="{ isTabActive: index == tTIndex }"
              @click="changeTimeTab(index)"
            >
              {{ item }}
            </div>
          </div>
        </div>
        <div style="flex-grow: 1">
          <lineChart ref="line" v-if="tTIndex == 0"></lineChart>
          <barChart ref="bar" v-if="tTIndex == 1"></barChart>
          <fanChart ref="fan" v-if="tTIndex == 2"></fanChart>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import lineChart from "./components/totalReportChart/lineChart";
import barChart from "./components/totalReportChart/barChart";
import fanChart from "./components/totalReportChart/fanChart";
import {
  areaList,
  companyList,
  companyelecstation,
  elecEqmList,
  pointList,
  zlcharts,
} from "@/network/totalReport";
export default {
  components: {
    lineChart,
    barChart,
    fanChart,
  },
  data() {
    return {
      timeTab: ["折线统计图", "柱状统计图", "扇形统计图"],
      tTIndex: 0,
      areaOptions: [],
      areaVal: "",
      companyOptions: [],
      companyVal: "",
      elecstationOptions: [],
      elecstationVal: "",
      elecEqmOptions: [],
      elecEqmVal: "",
      pointListOptions: [],
      pointVal: "",
      date1: [],
      tableData: [],
    };
  },
  mounted() {
    // this.date2 = this.date1 =
    //   this.$getDate().year +
    //   "-" +
    //   this.$getDate().month +
    //   "-" +
    //   this.$getDate().day;
    this.areaListFun();
  },
  methods: {
    changeTimeTab(index) {
      this.tTIndex = index;
      this.zlchartsFun();
    },
    areaListFun() {
      areaList()
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.areaOptions = content;
          }
        })
        .catch((err) => {});
    },
    companyListFun() {
      this.companyVal = ''
      companyList(this.areaVal)
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.companyOptions = content.companyList;
          }
        })
        .catch((err) => {});
    },
    companyelecstationFun() {
      this.elecstationVal = ''
      companyelecstation(this.companyVal)
        .then((res) => {
          const { elecstation, errmsg, errcode } = res;
          if (errcode == 0) {
            this.elecstationOptions = elecstation;
          }
        })
        .catch((err) => {});
    },
    elecEqmListFun() {
      this.elecEqmVal = ''
      elecEqmList(this.elecstationVal)
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.elecEqmOptions = content;
          }
        })
        .catch((err) => {});
    },
    pointListFun() {
      this.pointVal = ''
      pointList(this.elecEqmVal)
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            this.pointListOptions = content;
          }
        })
        .catch((err) => {});
    },
    zlchartsFun() {
      zlcharts(
        this.areaVal,
        this.companyVal,
        this.date1[0].split(" ")[0],
        this.date1[0].split(" ")[1],
        this.tTIndex,
        this.elecEqmVal,
        this.pointVal,
        this.elecstationVal,
        this.date1[1].split(" ")[0],
        this.date1[1].split(" ")[1]
      )
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            console.log(content);
            this.tableData = content.line;

            if (this.tTIndex == 0) {
              this.$refs.line.fillChart1(content.data12);
            } else if (this.tTIndex == 1) {
              this.$refs.bar.fillChart1(content.data12);
            }else if (this.tTIndex == 2) {
              this.$refs.fan.fillChart1(content.data3);
            }
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
