<template>
  <div id="main"></div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    fillChart1(data) {
      let chart_projectLoad = this.$echarts.init(
        document.getElementById("main")
      );
      var option = {
        grid: {
          left: "5%",
          right: "4%",
          bottom: "3%",
          top: "0%",
          containLabel: true,
        },
        title: {
          text: "",
          subtext: "",
          left: "center",
        },
        tooltip: {
          trigger: "item",
        },
        legend: {
          textStyle: {
            color: "#fff",
          },
          orient: "vertical",
          left: "left",
          type: "scroll",
          orient: "vertical",
        },
        series: [
          {
            left: "25%",
            name: "",
            type: "pie",
            radius: "50%",
            data: data,
            label: {
              fontWeight: "bold",
              color: "#ffffff",
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      };

      chart_projectLoad.setOption(option);
    },
  },
};
</script>
<style scoped>
#main {
  width: 100%;
  height: 100%;
}
</style>