<template>
  <div id="main"></div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    fillChart1(data) {
      let chart_projectLoad = this.$echarts.init(
        document.getElementById("main")
      );
      var dateList = data.map(function (item) {
        return item[0];
      });
      var valueList = data.map(function (item) {
        return item[1];
      });

      var option = {
        xAxis: {
          type: "category",
          data: dateList,
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
        },
        dataZoom: [
          {},
          {
            type: "inside",
          },
        ],
        tooltip: {
          trigger: "axis",
        },
        yAxis: {
          type: "value",
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
        },
        series: [
          {
            data: valueList,
            type: "bar",
          },
        ],
      };

      chart_projectLoad.setOption(option);
    },
  },
};
</script>
<style scoped>
#main {
  width: 100%;
  height: 100%;
}
</style>